import {
  IDashboardDetail,
  IWidgetDetail,
} from '../model/interface/workspace.interface';
import {getConfig} from './httpClient';

export const createWorkspaceServices = (fetchRequest: any) => ({
  async getListDashboard(page: number) {
    const endpoint = `/services/workspace/api/dashboards?page=${page}&size=20&sort=indexSort,desc`;
    try {
      const data = await fetchRequest({
        url: getConfig().BASE_URL_WORKSPACE + endpoint,
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        },
      });
      return Promise.resolve({
        data: data?.data as IDashboardDetail[],
        totalItems: Number(data?.headers.get('x-total-count') || 0),
      });
    } catch (error) {
      Promise.reject(error);
    }
  },

  async getDashboardDetail(id: string) {
    const endpoint = `/services/workspace/api/dashboards-detail/${id}`;
    try {
      const data = await fetchRequest({
        url: getConfig().BASE_URL_WORKSPACE + endpoint,
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        },
      });
      return Promise.resolve(data?.data as IDashboardDetail);
    } catch (error) {
      Promise.reject(error);
    }
  },

  async getWidgetsByDashboard(id: string) {
    const endpoint = `/services/workspace/api/dashboards/${id}?sort=createdDate,asc`;
    try {
      const data = await fetchRequest({
        url: getConfig().BASE_URL_WORKSPACE + endpoint,
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        },
      });
      return Promise.resolve(data?.data as IWidgetDetail[]);
    } catch (error) {
      Promise.reject(error);
    }
  },
});
