import {useEffect, useState} from 'react';
import {useWorkspaceServices} from '../../../hooks/apiHooks/useAPI';
import {IWidgetDetail} from '../../../model/interface/workspace.interface';
import {Icon, List, ListItem} from '@ui-kitten/components';
import HeaderCM from '../../components/Header/HeaderCM';
import {useRoute} from '@react-navigation/native';
import Skeleton from '../../components/Skeleton';
import ModalFullScreen from '../../components/ModalFullScreen';
import WidgetDetailScreen from './component/widget-detail';
import Toast from 'react-native-toast-message';
import PreviewWidget from './component/preview-widget';
import {SafeAreaView} from 'react-native-safe-area-context';
import {TouchableOpacity} from 'react-native';

const IOCWidgetScreen = () => {
  const router = useRoute();
  const {dashboardId} = router?.params as any;
  const [loading, setLoading] = useState<boolean>(false);
  const [widgets, setWidgets] = useState<IWidgetDetail[]>([]);
  const [isModalVisible, setIsModalVisible] = useState<boolean>(false);
  const [selectedWidget, setSelectedWidget] = useState<IWidgetDetail>();
  const WorkspaceService = useWorkspaceServices();

  useEffect(() => {
    getListWidgets();
  }, []);

  const getListWidgets = async () => {
    try {
      setLoading(true);
      const dataResp = await WorkspaceService.getWidgetsByDashboard(
        dashboardId,
      );
      if (dataResp) {
        setWidgets(dataResp);
      }
    } catch (error) {
    } finally {
      setLoading(false);
    }
  };

  const handleSelectWidget = (widget: IWidgetDetail) => {
    if (widget.serviceName !== 'eAnalysis') {
      Toast.show({
        type: 'warning',
        text1: 'Thông báo',
        text2: `Widget này thuộc dịch vụ ${widget.serviceName} đang được phát triển`,
      });
      return;
    }
    setSelectedWidget(widget);
    setIsModalVisible(true);
  };

  return (
    <SafeAreaView
      edges={['bottom']}
      style={{flex: 1, backgroundColor: 'white'}}>
      <HeaderCM title="Widgets" />
      <List
        data={widgets}
        renderItem={({item}) => (
          <TouchableOpacity onPress={() => handleSelectWidget(item)}>
            <PreviewWidget widget={item} />
          </TouchableOpacity>
        )}
        ListEmptyComponent={<Skeleton />}
      />

      <ModalFullScreen
        isVisible={isModalVisible}
        children={
          <WidgetDetailScreen
            onClose={() => setIsModalVisible(false)}
            widget={selectedWidget}
          />
        }
      />
    </SafeAreaView>
  );
};

export default IOCWidgetScreen;
