import HeaderCM from '../../../../components/Header/HeaderCM';
import WebView from 'react-native-webview';
import {getConfig} from '../../../../../services/httpClient';
import Color from '../../../../../constants/Color';
import {IWidgetDetail} from '../../../../../model/interface/workspace.interface';
import {String} from '../../../../../constants';

type Props = {
  widget?: IWidgetDetail;
  onClose: () => void;
};

const WidgetDetailScreen = ({onClose, widget}: Props) => {
  const params = JSON.parse(widget?.param || '{}');

  return (
    <>
      <HeaderCM
        title={`${widget?.widgetName}`}
        style={{backgroundColor: Color.White}}
        contentStyle={{backgroundColor: Color.White}}
        titleStyle={{color: Color.Black}}
        fillIconBackLeft={Color.Black}
        onPressIconLeft={onClose}
      />
      <WebView
        source={{
          uri: `${
            getConfig().BASE_URL_EANALYSIS
          }superset_app/superset/dashboard/${
            params?.configAdvanced?.reportDetail
          }/?standalone=true&${
            params?.configAdvanced?.darkMode ? 'theme=dark' : 'theme=light'
          }`,
        }}
        style={{flex: 1}}
        injectedJavaScript={String.SCRIPT_SCALE_WEBVIEW}
        javaScriptEnabled
        domStorageEnabled
        mediaPlaybackRequiresUserGesture={false}
        startInLoadingState
        sharedCookiesEnabled
        thirdPartyCookiesEnabled
      />
    </>
  );
};

export default WidgetDetailScreen;
