import {View} from 'react-native';
import TextCM from '../components/Text';
import HeaderCM from '../components/Header/HeaderCM';
import {Icon, List, ListItem} from '@ui-kitten/components';
import {useEffect, useState} from 'react';
import {IDashboardDetail} from '../../model/interface/workspace.interface';
import {useWorkspaceServices} from '../../hooks/apiHooks/useAPI';
import {useNavigation} from '@react-navigation/native';

const IOCApp = () => {
  const [page, setPage] = useState<number>(0);
  const [loading, setLoading] = useState<boolean>(false);
  const [refreshing, setRefreshing] = useState<boolean>(false);
  const [totalDashboard, setTotalDashboard] = useState<number>(0);
  const [dashboards, setDashboards] = useState<IDashboardDetail[]>([]);

  const navigation = useNavigation<any>();

  const WorkspaceService = useWorkspaceServices();

  useEffect(() => {
    getListDashboard(0);
  }, []);

  const getListDashboard = async (page: number) => {
    try {
      setLoading(true);
      const dataResp = await WorkspaceService.getListDashboard(page);
      if (dataResp) {
        if (page === 0) {
          setDashboards(dataResp.data);
          setTotalDashboard(dataResp.totalItems);
        } else {
          setDashboards([...dashboards, ...dataResp.data]);
        }
      }
    } catch (error) {
    } finally {
      setLoading(false);
      setRefreshing(false);
    }
  };

  const onRefresh = async () => {
    setRefreshing(true);
    setPage(0);
    getListDashboard(0);
  };

  const onEndReached = () => {
    if (dashboards.length < totalDashboard) {
      getListDashboard(page + 1);
      setPage(page + 1);
    }
  };
  return (
    <>
      <HeaderCM title="Danh sách dashboard" />
      <List
        data={dashboards}
        onRefresh={onRefresh}
        refreshing={refreshing}
        onEndReached={onEndReached}
        onEndReachedThreshold={0.2}
        renderItem={({item}) => (
          <ListItem
            title={item.name}
            accessoryRight={
              <Icon name="arrow-forward-outline" width={20} height={20} />
            }
            accessoryLeft={
              <Icon name="layout-outline" width={20} height={20} />
            }
            onPress={() =>
              navigation.navigate('IOCWidgetScreen', {dashboardId: item.id})
            }
          />
        )}
      />
    </>
  );
};

export default IOCApp;
